.header {
  &__block {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__title {
    font-size: 1.625rem;
    font-weight: 600;
    line-height: 31px;
    letter-spacing: 0.02em;
    text-align: left;
    margin-right: 10px;
  }
  &__icon {
    height: 19px;
    width: 19px;
    svg {
      width: 100%;
      height: 100%;
      fill: white;
    }
  }
  &__button:last-child {
    margin: 0;
  }
  &__menu-2 {
    display: none;
  }
}

@media (max-width: 1024px) {
  .header {
    &__block {
    }
    &__title {
      font-size: 1.2rem;
      font-weight: 600;
      line-height: 31px;
      letter-spacing: 0.02em;
      text-align: left;
      margin-right: 0.625rem;
    }
    &__icon {
    }
    &__button:last-child {
      margin: 0;
    }
  }
}
@media (max-width: 640px) {
  .header {
    &__block {
    }
    &__title {
      font-size: 1.2rem;
      font-weight: 600;
      line-height: 31px;
      letter-spacing: 0.02em;
      text-align: left;
      margin-right: 10px;
      display: none;
    }
    &__icon {
      height: 30px;
      width: 30px;
      svg {
        width: 100%;
        height: 100%;
        fill: white;
      }
    }
    &__button:last-child {
      margin: 0;
    }
    &__menu-1 {
      display: none;
    }
    &__menu-2 {
      display: block;
    }
  }
}
