.info {
  padding: 6.25rem 0px;
  &__block {
    display: grid;
    grid-template-columns: 400px 1fr;
    align-items: center;
  }

  @media (max-width: 1024px) {
    .info {
      &__block {
        display: grid;
        grid-template-columns: 1fr;
      }
      &__title {
        margin-bottom: 1.25rem;
      }
    }
  }

  @media (max-width: 640px) {
    .info {
      &__title {
        text-align: center;
      }
    }
  }
}
