.footer {
  padding: 4.125rem 0px;

  &__line {
    width: 100%;
    background-color: rgba(20, 24, 62, 0.15);
    height: 1px;
  }
  &__title {
    font-size: 1.3rem;
    font-weight: 700;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 30px;
    color: $color-grin;
    margin-bottom: 3rem;
  }
  &__title-info {
    font-size: 1rem;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 30px;
    color: $color-grin;
  }
}
