.sliderChapter {
  padding: 6.25rem 0px;
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */
    text-align: center;
    letter-spacing: 0.02em;
    margin-bottom: 1.875rem;
    color: $color-grin;
  }

  .swiper-slide {
    box-sizing: border-box;
    border-radius: 5px;
    display: flex;
    min-height: 400px;
    flex-direction: column;
    border: 2px solid rgba(47%, 52%, 49%, 0.1);

    .slider__img {
      width: 100%;
      display: block;
      height: 200px;
      position: relative;
      img {
        border-radius: 5px 5px 0px 0px;
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }

    .slider__text {
      padding: 10px;
    }
  }
}
