.hero {
  background-image: url('./.././../../../../../public/img/bg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: -75px;
  height: 852px;
  padding: 75px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 2.25rem;
    text-align: center;
    letter-spacing: 0.02em;
    color: #ffffff;
    margin-bottom: 1.875rem;
  }
  &__title {
    font-style: normal;
    font-weight: 600;
    font-size: 5.625rem;
    text-align: center;
    /* identical to box height */
    letter-spacing: 0.04em;
    color: #ffffff;
    margin-bottom: 1.875rem;
  }
  &__about {
    font-style: normal;
    font-weight: 400;
    font-size: 1.375rem;
    line-height: 40px;
    text-align: center;
    margin-bottom: 1.875rem;

    color: #ffffff;

    opacity: 0.6;
  }

  &__button-text {
    font-style: normal;
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 24px;
    /* identical to box height */

    letter-spacing: 0.02em;

    color: #ffffff;

    text-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
  }
}

@media (max-width: 1024px) {
  .hero {
    &__subtitle {
      font-size: 2rem;
    }
    &__title {
      font-size: 3.625rem;
    }
    &__about {
    }

    &__button-text {
    }
  }
}

@media (max-width: 640px) {
  .hero {
    &__subtitle {
      font-size: 1.7rem;
    }
    &__title {
      font-size: 2.425rem;
    }
    &__about {
      display: none;
    }

    &__button-text {
    }
  }
}
