.center {
  padding: 6.25rem 0px;
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */

    letter-spacing: 0.02em;
    margin-bottom: 1.875rem;
    color: $color-grin;
  }
  &__blog-2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
  }
  &__item {
    width: 100%;
    height: auto;
    border-radius: 8px;
    transition: 0.3s all ease;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border: 2px solid rgba(47%, 52%, 49%, 0.1);
    box-shadow: 4px 4px 18px 0px rgba(120, 132, 125, 0.2);
    h2 {
      margin-top: 1.25rem;

      font-size: 1rem;
      font-weight: 400;
      line-height: 32px;
      letter-spacing: 0em;
      text-align: left;
    }
  }

  &__item:hover {
    z-index: 10;
    transition: 0.3s all ease;
    transform: scale(1.2);
    border: 2px solid rgba(47%, 52%, 49%, 0.1);
    box-shadow: 4px 9px 30px 23px rgba(120, 132, 125, 0.2);
    background-color: white;
  }
}

@media (max-width: 1024px) {
  .center {
    &__blog-2 {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media (max-width: 640px) {
  .center {
    &__blog-2 {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
}
