.right {
  padding: 6.25rem 0px;
  &___content {
    display: grid;
    grid-template-columns: auto 60%;
    align-items: center;
    justify-content: space-between;
  }
  &___blog-2 {
    height: 400px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &___title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */

    letter-spacing: 0.02em;

    color: $color-grin;
  }
  &___list {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  &___item {
    font-style: normal;
    font-weight: 400;
    font-size: 1.375rem;
    line-height: 40px;
    /* or 182% */
    color: $color-gray;
    position: relative;
    padding-left: 30px;
  }
  &___item::before {
    content: '';
    position: absolute;
    display: block;
    top: 50%;
    left: 5px;
    transform: translate(0, -50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $color-gray;
  }
}

@media (max-width: 640px) {
  .right {
    padding: 6.25rem 0px;
    &___content {
      display: grid;
      grid-template-columns: 1fr;
      align-items: center;
      justify-content: center;
    }
    &___blog-2 {
      display: none;
    }
    &___title {
      text-align: center;
    }
    &___list {
    }
    &___item {
      padding-left: 100px;
    }
    &___item::before {
      left: 70px;
    }
  }
}
@media (max-width: 437px) {
  .right {
    padding: 6.25rem 0px;
    &___content {
    }
    &___blog-2 {
    }
    &___title {
      text-align: center;
    }
    &___list {
    }
    &___item {
      padding-left: 23px;
    }
    &___item::before {
      left: 5px;
    }
  }
}
